import * as api from "../lib/web3js/index";
import { show, hide } from "react-functional-modal";
import ChainInteraction from "../components/ChainInteraction/ChainInteraction";
import {EVENT_TRANS_BEGIN, EVENT_TRANS_END, MODAL_OPTIONS} from '../utils/Constants'
import Emitter from "../utils/Emitter";

class ApiProxy {
  status = 'waiting'
  async request(method,params=[],options = {}){
    const apis = await import('../lib/web3js/index')
    let res = null;
    const {subject,onAccept,onReject,onWarning,approveTip,title,content } = options
    if(options.write) {
      params.push({
        onAccept : hash => {
          this.show(subject,'success',approveTip)
          window.setTimeout(() => {
            this.close(subject)
            onAccept && onAccept();
            Emitter.emit(EVENT_TRANS_BEGIN,{title : title,content : content,hash : hash})
          },2000)
          
        },
        onReceipt : () => {
          this.close(subject)
        },
        onReject : () => {
          this.show(subject,'reject',approveTip)
          window.setTimeout(() => this.close(subject),2000)
          onReject && onReject()
        },
        onWarning : () => {
          this.show(subject,'warning',approveTip)
          onWarning && onWarning();
        }
      })
      this.show(subject,'pending',approveTip)

    }
    try {
      const start = new Date().getTime();
      options.debugger && console.log(`%c start execute ${method} at ${start}`,'background : orange ; color : #CE5E5E')
      res = await apis[method].call(this,...params)
      options.debugger && console.log(`%c end execute ${method} and take time : ${new Date().getTime() - start}`,'background: #0A172B; color: #59AE99')
    } catch(e){
      console.log(e)
    }
    res = this.processResponse(res,options)
    if(options.write && options.includeResponse){
      Emitter.emit(EVENT_TRANS_END,{title : title , content : content,context : res })
    }
    return res
  }

  syncRequest(method,params = [], options ={}) {
    const res = api[method].call(this,...params)  
    return this.processResponse(res,options)
  }

  close (subject){
    hide(this.getMessageKey(subject))
  }

  getMessageKey(subject){
    return `transaction-box-${subject.split(/\s+/).join('-')}`
  }

  show(subject,status,approveTip){
    const key = this.getMessageKey(subject)
    this.close(key);
    const options = {
      ...MODAL_OPTIONS,
        style : {
          background: "rgba(0, 0, 0, 0.4)" ,
          zIndex : 1001,
        },
        key : key
    }
    show(<ChainInteraction title={subject} status={status} options={{approveTip}} close={() => this.close(subject)}/>,options)
  }

  

  processResponse(res,options){
    if(options.includeResponse){
      if(res && res.response){
        return res
      } 
    } else {
      if(res && res.response){
        return res.response.data
      } 
    }
    return res
  }
}

export default new ApiProxy();